        this.flagI = null
        //得到省的值
        this.prov = item.text
        //把当前市的数组赋值给this.allArray[1].Arr
        this.allArray[1].Arr = item.children
      }else if(i==1){
        this.flag = 2
        this.flagI = null
        this.city = item.text
        this.allArray[2].Arr = item.children
      }else{
        this.flag = 2
        //设置最后点击的样式
        this.flagI = j
        this.district = item.text
        //关闭当前模板数据
        this.ishave = false

        //设置当前要抛出去的数据
        this.data = {
          prov:this.prov,
          city:this.city,
          district:this.district
        }
      }
      

    },
    choicesClick1(){
      this.flag = 0
      this.city = "请选择"
      this.district = "请选择"
    },
    choicesClick2(){
